<template>
  <div class="stats tank">
    <ul>
      <li v-for="(stat, index) in tank.water" :key="stat.id">
        <label>
          {{ stat.title }}
          <span class="stat-value" v-show="stat.edit == false">
            {{ stat.value }}
          </span>
          <span v-show="stat.edit == true">
            <!--<input maxlength="3" type="number" step="5" min="0" max="100" v-model="stat.value"-->
            <input maxlength="10000" type="number" step="1" min="0" max="100" v-model="stat.value"
            v-on:blur= "stat.edit=false; $emit('update')"
            @keyup.enter = "stat.edit=false; $emit('update')">
          </span>
          <button>
            <span @click="stat.edit = true" v-show="stat.edit == false">Edit</span>
            <span @click="stat.edit = false" v-show="stat.edit == true">Save</span>
          </button>
        </label>
      </li>
    </ul>
    <hr>
    <p>{{ changeWaterMessage }}</p>
    
    <!-- The pretty part -->
    <div class="tank-container">
      <div class="tank">
        <div class="max-water-level"></div>
        <div class="actual-water-level"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'StatsTank',
  data () {
    return {
      // tank object with stings and values
      tank: {
        water: [
          {'name': '', 'title': 'Max Water Level', 'value': (20 * 5), 'edit': false}, // 20 = %5
          {'name': '', 'title': 'Actual Water Level', 'value': (18 * 5), 'edit': false}, // 18 * 5 = %90
          {'name': '', 'title': 'Change water ', 'value': (4 * 5), 'edit': false}, // 4 * %5 = 20%
          {'name': 'changeOccurrence', 'title': 'Change occurrence (days)', 'value': 17, 'edit': false} // 14 = 2 weeks. 15 = 2 weeks 1 day
        ]
      },
      editedStat: null
    }
  },
  methods: {
    editStat: function (stat) {
      this.editStat = stat
    }
  },
  computed: {
    changeWaterMessage: function () {
      let waterChange = this.tank.water[3].value
      let waterChangePercent = this.tank.water[2].value
      let weeks = Math.floor(waterChange / 7)
      let days = waterChange % 7
      if (weeks === 0) {
        if (days === 1) {
          return `Change ${waterChangePercent}% every day`
        } else if (days > 1) {
          return `Change ${waterChangePercent}% every ${days} days`
        }
      } else {
        if (weeks === 1) {
          return `Change ${waterChangePercent}% every ${weeks} week and ${days} days`
        } else if (weeks > 1) {
          return `Change ${waterChangePercent}% every ${weeks} weeks and ${days} days`
        }
      }
    }
  }
}
</script>

<style>
  li {
    margin: 0;
    display: block;
  }
  li input {
    width: 40px;
    cursor: pointer;
  }
  /*li .stat-value:hover, li .stat-value:focus {*/
  /*  background: #888;*/
  /*  color: white;*/
  /*  cursor: pointer;*/
  /*}*/
  .stat-value {
    color: deepskyblue;
  }
</style>


